import React from 'react';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import Navbar from './components/Navbar';
import IconBar from './components/IconBar';
import About from './components/About';
import Education from './components/Education';
import Contact from './components/Contact';
import EllerbeCreek from './components/Experience/EllerbeCreek';
import JPHolding from './components/Experience/JPHolding'
import SeniorLifeInsurance from './components/Experience/SeniorLifeInsurance'

function App() {
  return (
    <>
      <div className="app">
        <div className="header">
          <h1>Peter Zheng's Portfolio</h1>
        </div>
        <div className="row">
          <IconBar />
          <Navbar />
          <Router>
            <Switch> {/* Use 'Switch' here for v5 */}
              <Route exact path="/" component={About} />
              <Route path="/education" component={Education} />
              <Route path="/ellerbecreek" component={EllerbeCreek} />
              <Route path="/jpholding" component={JPHolding} />
              <Route path="/seniorlifeinsurance" component={SeniorLifeInsurance} />
              <Route path="/contact" component={Contact} />
            </Switch>
          </Router>
        </div>
        <div className="footer">
          <p>UTF-8</p>
          <p>JavaScript</p>
          <p>Prettier</p>
        </div>
      </div>
    </>
  );
}


export default App;
